<template>
  <div class="APP">
    <div class="box">
      <div class="card">
        <h1 style="margin-bottom: 20px; font-weight: bold;" @click="toggleText">{{ title }}</h1>
        <p
          style="color: gray; font-size:13px ; white-space: nowrap;"
        >{{ isPink ? '一键跳转！！！' : '网站收集' }}</p>

        <a
          v-for="(item, index) in font"
          :key="index"
          :href="item.url"
          :style="{ backgroundColor: isPink ? 'black' : '#3CB371', color: 'white' }"
          target="_blank"
        >{{ item.name }}</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "APP",
  data() {
    return {
      isPink: true,
      title: "Anime website",
      font: [
        { name: "樱花动漫", url: "https://yhdmapps.com" },
        { name: "哔哩哔哩", url: "https://www.bilibili.com/" },
        { name: "AcFun", url: "https://www.acfun.cn/" },
        { name: "巴哈姆特動畫瘋", url: "https://ani.gamer.com.tw/" }
      ]
    };
  },
  mounted() {
    this.intervalId = setInterval(() => {
      this.isPink = !this.isPink;
    }, 10000);
  },
  beforeDestroy() {
    clearInterval(this.intervalId);
  },
  methods: {
    toggleText() {
      this.title =
        this.title === "Anime website" ? "动漫网址" : "Anime website";
    }
  }
};
</script>

